<template>
	<!-- 日期选择 -->
	<view class="dateSelector">
		<view class="box" @tap="selectShow = true">
			<text class="year">2021</text>
			<text class="iconfont iconxiala"></text>
		</view>
		<u-select v-model="selectShow" mode="mutil-column" :list="list" @confirm="confirm"></u-select>

	</view>

</template>

<script>

	export default {
		name: "dateSelector",
		data() {
			return {
				selectShow: false,
				// 选择器选择的月份 传递给swiper current值
				swiperIndex: 0,
				list: [
					[{
						value: '2021',
						label: '2021'
					}],
					[{
							value: '1',
							label: '1月'
						},
						{
							value: '2',
							label: '2月'
						}, {
							value: '3',
							label: '3月'
						}, {
							value: '4',
							label: '4月'
						}, {
							value: '5',
							label: '5月'
						}, {
							value: '6',
							label: '6月'
						}, {
							value: '7',
							label: '7月'
						}, {
							value: '8',
							label: '8月'
						}, {
							value: '9',
							label: '9月'
						}, {
							value: '10',
							label: '10月'
						}, {
							value: '11',
							label: '11月'
						}, {
							value: '12',
							label: '12月'
						},
					],

				],
			};
		},
		methods: {
			confirm(e) {
				this.swiperIndex = Number(e[1].value);
				this.swiperDate = Number(e[0].value);

				this.$emit('selectDateInfo', {
					date: this.swiperDate,
					index: this.swiperIndex
				});
			}
		},
	}
</script>

<style scoped lang="scss">
	@import url('@/iconfonts/iconfont.css');

	.dateSelector {
		padding: 100rpx 0;

		.box {
			height: 60rpx;
			text-align: center;

			.year {
				height: 60rpx;
				line-height: 60rpx;
				margin-right: 10rpx;
				font-weight: bold;
			}

			.iconxiala {
				height: 40rpx;
				line-height: 40rpx;
			}
		}
	}
</style>
